.sidebar {
  box-sizing: border-box;
  min-height: 100%; }
.nav {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding-left: 0;
  li {
    a {
      color: #ffffff;
      text-decoration: none;
      &:hover {
        text-decoration: underline; }
      &.active {
        color: yellow;
        &:hover {
          text-decoration: none; } } } } }



